*{
    box-sizing: border-box;
    margin: 0;
}

.content{
    display: flex;
    flex-direction: column;
    background: url('../img/prague.jpg') center fixed;
    background-size: cover;
    font-family: 'Roboto Condensed', sans-serif;
/*     width: 100vw;
    height: 100vh; */
}

.welcome{
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    height: 10em;

    @media screen and (max-width: 800px){
       
        display: flex;
        flex-direction: column;
       /*  justify-content: center; */
        align-items: center;
        height: 100%;
        
        
    }
    /* background: white; */
       
    a{  display:flex;
        background-color:rgba(224,171,119, 0.5);
        height: 3rem;
        padding: 1rem;
        padding-right: 2rem;
        width:16%;
        color: white;
        text-decoration: none;
        box-shadow: 5px 3px 5px -3px rgba(145,145,145,1);

        @media screen and (max-width: 800px){
            text-align: center;
            width:100%;

        }

        &.home{
            position:fixed;
            background-color: rgba(222,101,74, 0.7);            
        }
    }
  
}

.banner{
    position: relative;
    display:flex;
    background-color: rgba(145,145,145,0.4);
    background-size: cover;
    width: 100%;
    height: 20em;

    .titulek{
        position:absolute;
        left: 11rem;
        top: 3rem;
        color: rgb(243, 235, 235);
        font-style: italic;
        font-size: 1.5em;
     

        img{
            position:absolute;
            left: 45rem;
            top:5rem;
           /*  bottom: 1px;
            margin-left: 94%; */
            
        }
    }

}

.quote{
    display:flex;
    /* background-color: rgba(0, 0, 0,0.1); */
    

    p{
        padding: 7rem;
        color:white;
        font-family: 'Roboto', sans-serif;
        font-weight: lighter;
        font-size: 1.5rem;
        background-color: rgba(0, 0, 0,0.1);
    }
}

.colums{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    color: white;
    height: 20rem;
    background-color:rgba(131, 89, 49,0.4);
    padding-left: 100px;
    padding-right: 100px;
}



.half_circle{
    position: relative;
    display:flex;
    flex-direction: column;
    width: 105px;
    
}

.pulkruh{
    position: relative;
    bottom: 50px;
    width: 100px;
    height: 100px;
   /*  background-color: rgba(131, 89, 49,0.8); */
    background-image: /* linear-gradient(rgba(224, 206, 206, 0.1),  */rgb(235, 125, 51);
    background-color: rgba(235, 125, 51, 0.5);
    border-radius: 100%;
    border: 2px solid rgba(255,255,255,0.5)
}

.skills_container{
    display:flex;
    flex-direction: row;
    justify-content: center;
}

 .skills{
    width:80%;
    display:flex;
    color: white;
    flex-direction: column;
    
    /* text-align:center; */
    
    h1{
        text-align: center;
        padding: 2rem;
    }

} 

.label{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 12rem;
    height: 3rem;
    margin-bottom: 1rem;
    background-color: rgba(145,145,145,0.7);


}

