        width: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        color: white;
        text-align: center;
        padding: 2rem;
    }

.portfolio_cards_container{
    display:flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
}
 
 .card {
        width: 26%;
        height: 15rem;
        display: flex;
        flex-direction: column-reverse;
        margin: 0.5rem;
        border: 0.5px solid white;
}

.card_bottom{
    height: 33%;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.card_top{
    width:100%;
    height:100%;
  /*   background: url('../img/kafe.jpg') center fixed;
    background-size: cover; */

    img{
        max-width:100%;
    height:auto;
    }

}

.about_me{
    /* display:flex;
    flex-direction: row; */
    justify-content: center;
    flex-wrap: wrap;
    height: 40rem;
    width: 100%;
    background-color:rgba(145,145,145,0.5);
    color: white;
    
    h1{
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
    }
}

.about_me_content{
display:flex;
flex-direction: row;
justify-content: center;
align-items: center;
align-content: center;
flex-wrap: nowrap;
width: 90%;
.obrazek{
    background: url('../img/my.jpg') center;
    background-size: cover;
    border-radius: 100%;
    width:25rem;
    height: 25rem;
    padding:3rem;
    margin-right: 2rem;
    transform: rotate(270deg);
}
.about_me_text{
    position: relative;
    bottom: 0.5rem;
    width:15rem;
    margin-left: 2rem;
    text-align: center;
    padding-bottom: 2rem;
    
}
}

.about_me_button{
    text-align: center;
    button {
    background-color:rgba(0, 0, 0, 0.5);
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;}
}





